import { Injectable } from '@angular/core';

@Injectable()
export class GameService {

  constructor() { }

  public initTargets(): number[] {
    const targets: number[] = [];
    for (let i = 0; i < 6; i++) {
      targets.push(this.getRandomNumberInterval(targets));
    }
    targets.push(25);
    return targets;
  }

  public initPlayers(players: string[]) {
    return players.map(p => {
      return {
        name: p,
        score: 0,
        points: [0, 0, 0, 0, 0, 0, 0]
      }
    });
  }

  public getRandomNumberInterval(currentNumbers: number[]): number {
    const min = 7;
    const max = 20;
    const randomNumber = Math.round(Math.random() * (max - min) + min);

    if (currentNumbers.indexOf(randomNumber) > -1) {
      return this.getRandomNumberInterval(currentNumbers);
    } else {
      return randomNumber;
    }
  }
}
