import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GameService } from './game.service';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.scss']
})
export class GameComponent implements OnInit {

  public players: { name: string, score: number, points: number[] }[];
  public targets: number[];
  public first: { skill: string, value: number } = { skill: '', value: 0 };
  public second: { skill: string, value: number } = { skill: '', value: 0 };
  public third: { skill: string, value: number } = { skill: '', value: 0 };
  public isValidateEnabled: boolean = false;

  constructor(
    private readonly route: ActivatedRoute,
    private readonly service: GameService
  ) { }

  public ngOnInit(): void {
    this.getPlayersFromRoute();
    this.targets = this.service.initTargets();
  }

  public getSkillClass({ skill }): string {
    let skillClass = '';
    switch (skill) {
      case 'S': {
        skillClass = 'simple';
      } break;
      case 'D': {
        skillClass = 'double';
      } break;
      case 'T': {
        skillClass = 'triple';
      } break;
    }
    return skillClass;
  }

  public addTargetValue(value: number) {
    const turn = this.getTurn();
    this[turn].value = value;
  }

  public addSkillValue(skillValue: string) {
    const turn = this.getTurn();
    this[turn].skill = skillValue;
  }

  public resetPlayerInstance(): void {
    this.first = { skill: '', value: 0 };
    this.second = { skill: '', value: 0 };
    this.third = { skill: '', value: 0 };
    this.isValidateEnabled = false;
  }

  private getPlayersFromRoute(): void {
    this.route.data.subscribe(_ => {
      const { data: { players } } = window.history.state;
      this.players = this.service.initPlayers(players);
    });
  }

  private getTurn(): 'first' | 'second' | 'third' {
    this.isValidateEnabled = false;
    if (this.first.skill === '' || this.first.value === 0) {
      return 'first';
    } else if ((this.first.skill !== '' || this.first.value !== 0) && (this.second.skill === '' || this.second.value === 0)) {
      return 'second';
    } else if (((this.first.skill !== '' || this.first.value !== 0) && (this.second.skill !== '' || this.second.value !== 0)) && this.third.skill === '' || this.third.value === 0) {
      this.isValidateEnabled = true;
      return 'third';
    }
  }
}
